import { IExposedProperty } from '@/components';
import { TCurrency, TDirection } from '@/models/common';
import { IEntries } from '@/models/property';
import { createStore } from '@stencil/store';
import { enUS, Locale } from 'date-fns/locale';
export type UserPreference = {
  language_id: string;
  currency_id: string;
};
export interface ILocalizationStore {
  currencies: TCurrency[];
  localizedWords: string[];
  dir: TDirection;
  selectedLocale: Locale;
  userPreferences: UserPreference;
  app_data: {
    token: string;
    property_id: number;
  };
  property: IExposedProperty;
  setup_entries: {
    arrivalTime: IEntries[];
    ratePricingMode: IEntries[];
    bedPreferenceType: IEntries[];
  };
}

const initialState: ILocalizationStore = {
  dir: 'LTR',
  selectedLocale: enUS,
  localizedWords: [],
  userPreferences: {
    currency_id: 'usd',
    language_id: 'en',
  },
  app_data: {
    token: '',
    property_id: null,
  },
  property: undefined,
  setup_entries: undefined,
  currencies: [],
};
const { state: app_store, onChange: onAppDataChange } = createStore<ILocalizationStore>(initialState);

export function changeLocale(dir: TDirection, locale: Locale) {
  document.body.dir = dir;
  app_store.dir = dir;
  app_store.selectedLocale = locale;
}
export function updateUserPreference(params: Partial<UserPreference>) {
  app_store.userPreferences = {
    ...app_store.userPreferences,
    ...params,
  };
}
export { onAppDataChange };
export default app_store;
